<template>
  <div class="teacher_wrap">
    <div class="header">
      <span class="impot_title">导入老师</span><i class="prop">支持从excel表格批量复制黏贴（一次最多导入150位老师）</i><i class="prop color" @click="showPopup = true">批量添加提示</i>
    </div>
    <div class="content">
      <table class="tableWrap">
        <thead>
          <tr>
            <th>序号</th>
            <th>教师姓名</th>
            <th>性别</th>
            <th>手机号</th>
            <th>编辑</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in list" :key="index" @click="tag = index">
            <td>{{index+1}}</td>
            <td>
              <input @paste="cellkeydown" class="input_set" type="text" v-model="item.teacherName" :placeholder="index === 0 ? '必填（输入或直接粘贴）' : ''">
            </td>
            <td>
              <input class="input_set" type="text" v-model="item.teacherSex" :placeholder="index === 0 ? '选填（男或女）': ''">
            </td>
            <td>
              <input class="input_set" type="number" v-model="item.teacherTelephoneNum" :placeholder="index === 0 ? '选填': ''">
            </td>
            <td class="delete_icon">
              <i v-if="!showDelete" class="el-icon-error change_delete_color" @click="deleteList(index)"></i>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="bottom_button">
      <span class="impot_button" @click="cleatData">清空</span>
      <span class="impot_button" @click="addData" >新增</span>
      <span class="impot_button" @click="submitData" >导入</span>
    </div>
    <popup :showTag="showPopup" :showTaglink="linkFun" :src="require('@/assets/import_teacher.gif')"></popup>
  </div>
</template>
<script>
import api from './api'
import Popup from '../../components/popup'
export default {
  components: { Popup },
  data () {
    return {
      list: [
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        }
      ],
      tag: null,
      showDelete: false,
      showPopup: false
    }
  },
  watch: {
    list: {
      handler: function (list, oldList) {
        if (list.length <= 1) {
          this.showDelete = true
        } else {
          this.showDelete = false
        }
      },
      deep: true
    }
  },
  methods: {
    linkFun () {
      this.showPopup = false
    },
    addData () {
      this.list.push(
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        }
      )
    },
    submitData () {
      var nameList = []
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].teacherName !== '') {
          nameList.push(this.list[i].teacherName)
        }
        if (this.list[i].teacherSex !== null && this.list[i].teacherSex !== undefined && this.list[i].teacherSex !== '') {
          if (this.list[i].teacherSex !== '男' && this.list[i].teacherSex !== '女') {
            this.$notify.error({
              title: '提示',
              message: '请输入男或女！'
            })
            return
          }
        }
      }
      if (nameList.length <= 0) {
        return
      }
      this.checkRepeat(nameList)
    },
    checkRepeat (arr) {
      var repeatList = this.removeDuplicatedItem(arr)
      if (repeatList.length > 0) {
        this.$confirm('有重复名字: ' + repeatList.join('、') + '。是否继续导入？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.importData()
        }).catch(() => {})
      } else {
        this.importData()
      }
    },
    importData () {
      var finishData = []
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].teacherName !== '') {
          var item = {
            teacherName: this.list[i].teacherName,
            teacherSex: this.list[i].teacherSex,
            teacherTelephoneNum: this.list[i].teacherTelephoneNum
          }
          finishData.push(item)
        }
      }
      if (finishData.length > 150) {
        this.$message.error('一次最多导入150位老师')
        return
      }
      api.importTeacher({schoolId: this.$route.params.schoolId, dataList: finishData}, (data) => {
        this.$alert('导入成功！', '提示', {
          confirmButtonText: '确定'
        })
        this.cleatData()
      }, (er) => {
        if (er.toUser !== undefined && er.toUser) {
          this.$message.error(er.message)
        } else {
          this.$message.error('导入失败')
        }
      })
    },
    removeDuplicatedItem (arr) {
      var newArr = arr.sort()
      var repeatList = []
      var ret = []
      for (var a = 0; a < newArr.length - 1; a++) {
        if (newArr[a] === newArr[a + 1]) {
          repeatList.push(newArr[a])
        }
      }
      for (var i = 0, j = repeatList.length; i < j; i++) {
        if (ret.indexOf(repeatList[i]) === -1) {
          ret.push(repeatList[i])
        }
      }
      return ret
    },
    deleteList (index) {
      this.list.splice(index, 1)
    },
    cleatData () {
      this.list = [
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        },
        {
          teacherName: '',
          teacherSex: '',
          teacherTelephoneNum: ''
        }
      ]
    },
    cellkeydown (event) {
      if (event.type === 'paste') {
        var content = document.querySelector('#textAreaXXDD')
        content.value = ''
        content.focus()
        var inputs = document.querySelectorAll('input')
        for (let i = 0; i < inputs.length; i++) {
          inputs[i].blur()
        }
        setTimeout(() => {
          this.fillData()
        }, 100)
      }
    },
    fillData () {
      var content = document.querySelector('#textAreaXXDD')
      var len = content.value.split('\n')
      var copyData = []
      for (let i = 0; i < len.length; i++) {
        var item = {
          teacherName: len[i].split(/\s+/)[0],
          teacherSex: len[i].split(/\s+/)[1],
          teacherTelephoneNum: len[i].split(/\s+/)[2]
        }
        copyData.push(item)
      }
      if (this.tag !== null && this.tag !== undefined) {
        this.list = this.list.splice(0, this.tag).concat(copyData)
      } else {
        this.list = copyData
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.teacher_wrap{
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding:20px;
  box-sizing: border-box;
}
.teacher_wrap .header{
  background: #fff;
  border-top: 4px solid #67c23a;
  padding: 0 20px;
  box-sizing: border-box;
}
.teacher_wrap .header .impot_title{
  line-height: 60px;
  font-size: 16px;
  font-weight: 600;
}
.teacher_wrap .header .prop{
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  margin-left: 15px;
  color: #ff595a;
}
.teacher_wrap .header .color{
    color: #fff;
    display: inline-block;
    background: #67c23a;
    border-radius: 5px;
    padding: 2px 5px;
    cursor: pointer;
}
.teacher_wrap .bottom_button{
  background: #fff;
  padding: 10px 0;
  margin-top: 5px;
}
.teacher_wrap .bottom_button .impot_button{
  display: inline-block;
  width: 100px;
  height: 36px;
  text-align: center;
  background: #67c23a;
  line-height: 36px;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 15px;
  box-sizing: border-box;
}
.teacher_wrap .bottom_button .impot_button:hover{
  background: #85ce61;
  border-color: #85ce61;
  color: #fff;
}
.teacher_wrap .bottom_button .impot_button:active{
  background: #5daf34;
  border-color: #5daf34;
  color: #fff;
}
.teacher_wrap .content{
  margin-top: 5px;
  background: #fff;
}
.teacher_wrap .content .tableWrap{
  width: 100%;
  border-spacing: 0;
}
.teacher_wrap .content .tableWrap thead tr th{
  height: 40px;
  text-align: center;
  background: #F9F9F9;
  vertical-align: middle;
  font-size: 14px;
  color: #333333;
  letter-spacing: 0;
  border: 1px solid #E8ECEF;
  display:table-cell;
}
.teacher_wrap .content .tableWrap thead tr th:nth-of-type(1){
  border-width:1px 0px 1px 1px;
  width: 8%;
}
.teacher_wrap .content .tableWrap thead tr th:nth-of-type(2){
  border-width:1px 0px 1px 1px;
  width: 28%;
}
.teacher_wrap .content .tableWrap thead tr th:nth-of-type(3){
  border-width:1px 0px 1px 1px;
  width: 28%;
}
.teacher_wrap .content .tableWrap thead tr th:nth-of-type(4){
  border-width:1px 0px 1px 1px;
  width: 28%;
}
.teacher_wrap .content .tableWrap thead tr th:nth-of-type(5){
  border-width:1px 1px 1px 1px;
  width: 8%;
}
.teacher_wrap .content .tableWrap tbody tr td{
  height: 40px;
  text-align: center;
  background: #fff;
  vertical-align: middle;
  font-size: 14px;
