}
.teacher_wrap .content .tableWrap tbody tr td:nth-of-type(1){
  border-width:0px 0px 1px 1px;
  width: 8%;
}
.teacher_wrap .content .tableWrap tbody tr td:nth-of-type(2){
  border-width:0px 0px 1px 1px;
  width: 28%;
}
.teacher_wrap .content .tableWrap tbody tr td:nth-of-type(3){
  border-width:0px 0px 1px 1px;
  width: 28%;
}
.teacher_wrap .content .tableWrap tbody tr td:nth-of-type(4){
  border-width:0px 0px 1px 1px;
  width: 28%;
}
.teacher_wrap .content .tableWrap tbody tr td:nth-of-type(5){
  border-width:0px 1px 1px 1px;
  width: 8%;
}
.teacher_wrap .content .tableWrap tbody tr .delete_icon .change_delete_color{
  color: #c7c5c5;
  font-size: 25px;
  cursor: pointer;
}
.teacher_wrap .content .tableWrap tbody tr .delete_icon .el-icon-error:hover{
  color: #67c23a;
}
</style>



